export default {
  name: 'Logs',
  data: function data() {
    return {
      entries: []
    };
  },
  watch: {
    '$app.logOutput': {
      deep: true,
      immediate: true,
      handler: function handler(logOutput) {
        this.entries = logOutput;
      }
    }
  },
  computed: {
    debugging: {
      get: function get() {
        return this.$app._debug;
      },
      set: function set() {
        this.$app._debug = !this.$app._debug;
      }
    }
  },
  methods: {
    flush: function flush() {
      this.$app.logOutput = [];
      this.entries = [];
    }
  }
};